@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&display=swap');

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

h1 {
  margin-top: 0;
  font-size: 30px;
  font-family: 'Montserrat', sans-serif;
}

h2 {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 20px;
  font-family: 'Montserrat', sans-serif;
}

h3 {
  margin-top: 0;
  font-size: 18px;
}

h4 {
  margin-top: 0;
  font-size: 16px;
}

h5 {
  margin-top: 0;
  font-size: 14px;
}

h6 {
  margin-top: 0;
  font-size: 12px;
}

.content {
  padding: 20px;
  margin: auto;
  width: 100%;
  text-align: center;
}

.shuffle {
  padding: 20px;
}

.shuffle > button {
  padding: 5px;
}

.lockBtn {
  margin: 10px;
  padding: 5px;
  width: 50px;
  height: 50px;
  text-decoration: none;
  border-radius: 4px;
  vertical-align: middle;
  font-size: 22px;
  font-family: 'Montserrat', sans-serif;
}

input {
  margin-bottom: 20px;
  padding: 5px;
}

.locked {
  background-color: #69ab65;
  color: white;
  border: none;
}

.unlocked {
  background-color: white;
  color: black;
  border: 1px solid black;
}



